import React from "react";

const Persons = ({ search, deleter }) => {
  if (search.length > 0) {
    return search.map((e) => (
      <li key={e.name}>
        {" "}
        {e.name} {e.number}
        <button onClick={() => deleter(e)}>delete me</button>
      </li>
    ));
  } else {
    return null;
  }
};

export default Persons;
