import axios from "axios";
const BASE_URL = "/api/persons";

const entryAll = () => {
  const request = axios.get(BASE_URL);
  return request.then((response) => response.data);
};
const entryNew = (obj) => {
  const request = axios.post(BASE_URL, obj);
  return request.then((response) => response.data);
};
const entryDelete = (id) => {
  const request = axios.delete(`${BASE_URL}/${id}`);
  return request.then((res) => res.status);
};

const entryEdit = (user) => {
  const request = axios.put(`${BASE_URL}/${user.id}`, user);
  return request.then((response) => response.data);
};

export default { entryNew, entryDelete, entryAll, entryEdit };
