import React, { useState, useEffect } from "react";
import Filter from "./Filter";
import PersonForm from "./PersonForm";
import Persons from "./Persons";
import Notification from "./Notification";
import phonebook from "./services/phonebook";

const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState("");
  const [newSearch, setNewSearch] = useState(persons);
  const [uMessage, SetUMessage] = useState(null);

  useEffect(() => {
    phonebook.entryAll().then((response) => {
      setNewSearch(response);
      setPersons(response);
    });
  }, []);

  const addName = (e) => {
    e.preventDefault();

    let nameCheck = persons.some(
      (e) => e.name.toUpperCase() === newName.toUpperCase()
    );

    if (nameCheck) {
      // lookup by name??
      const result = window.confirm(`Really change the number of ${newName}?`);
      if (result) {
        const user = persons.find(
          (x) => x.name.toUpperCase() === newName.toUpperCase()
        );
        const updatedUser = { ...user, number: newNumber };
        phonebook
          .entryEdit(updatedUser)
          .then((response) => {
            const pList = persons
              .filter((p) => p.id !== updatedUser.id)
              .concat(updatedUser);
            setNewSearch(pList);
            setPersons(pList);

            SetUMessage(`${newName}'s number has been updated to ${newNumber}`);
            setTimeout(() => {
              SetUMessage(null);
            }, 5000);
          })
          .catch((error) => {
            SetUMessage(error.response.data.error);
            setTimeout(() => {
              SetUMessage(null);
            }, 5000);
          });
      } else {
        setNewName("");
        setNewNumber("");
        return;
      }
    } else {
      let newContact = {
        name: newName,
        number: newNumber,
      };

      phonebook
        .entryNew(newContact)
        .then((response) => {
          console.log(response);
          setNewSearch(persons.concat(response));
          setPersons(persons.concat(response));
          SetUMessage(
            `${newContact.name}'s number has been added to the phonebook!`
          );
          setTimeout(() => {
            SetUMessage(null);
          }, 5000);
        })
        .catch((error) => {
          SetUMessage(error.response.data.error);
          setTimeout(() => {
            SetUMessage(null);
          }, 5000);
        });

      setNewName("");
      setNewNumber("");
    }
  };

  const handlePersonChange = (e) => {
    setNewName(e.target.value);
  };

  const handleNumberChange = (e) => {
    setNewNumber(e.target.value);
  };

  const handleDelete = ({ name, id }) => {
    const result = window.confirm(`Delete ${name}?`);
    if (result) {
      phonebook
        .entryDelete(id)
        .then((code) => {
          if (code === 204) {
            const updatedList = persons.filter((p) => p.id !== id);
            setNewSearch(updatedList);
            setPersons(updatedList);
            SetUMessage(`${name} has been deleted!`);
            setTimeout(() => {
              SetUMessage(null);
            }, 5000);
          }
        })
        .catch((e) => {
          SetUMessage(`ERROR: Entry ${name} has already been deleted`);
          setTimeout(() => {
            SetUMessage(null);
          }, 5000);
        });
    }
  };

  const handleSearch = (e) => {
    let re = new RegExp(`${e.target.value.toUpperCase()}`);
    let hello = persons.filter((x) => x.name.toUpperCase().match(re));

    setNewSearch(hello);
  };

  return (
    <div>
      <Notification message={uMessage} />
      <h2>Phonebook</h2>
      <Filter filter={handleSearch} />
      <h2>Add a new</h2>
      <PersonForm
        name={newName}
        onSubmit={addName}
        onNameChange={handlePersonChange}
        number={newNumber}
        onNumberChange={handleNumberChange}
      />
      <h2>Numbers</h2>
      <ul>
        <Persons search={newSearch} deleter={handleDelete} />
      </ul>
      ...
    </div>
  );
};

export default App;
